
import userEvent from "@testing-library/user-event";
import { useEffect, useState } from "react";
import { json } from "react-router";
import { useParams } from "react-router-dom";


function Detail() {
    const { id } = useParams();
    const [loading, setLoading] = useState(true);
    const [movie_data, setMovieData] = useState();

    const getMovie = async () => {
        const json = await (
            await fetch(`https://yts.mx/api/v2/movie_details.json?movie_id=${id}`)
        ).json();
        setMovieData(json);
        console.log(json);
        setLoading(false);

    }
    useEffect(() => {
        getMovie();
    }, [])


    return (

        <div>
            {loading ? <h1>Loading..</h1> :
                <div>

                    <img src={movie_data.data.movie.medium_cover_image} alt={movie_data.data.movie.title} />
                    <h1>{movie_data.data.movie.title}</h1>

                    <ul>
                        {movie_data.data.movie.genres.map((g) =>
                            <li key={g}>{g}</li>
                        )
                        }
                    </ul>

                    <h1>{movie_data.data.movie.description_full}</h1>

                </div>}
        </div>

    )
}

export default Detail;